import "./styles.css";
import { useSelector, useDispatch } from "react-redux";
import { decrease, increase } from "./action/actions";
// import {}

export default function App() {
  const dispatch = useDispatch();
  const value = useSelector((state) => state.counter_reducers);
  const onIncrease = () => {
    // increase();

    dispatch(increase());
  };
  const onDecrease = () => {
    dispatch(decrease());
  };
  return (
    <div className="App">
      <button onClick={onDecrease}>Decrease</button>
      {value}
      <button onClick={onIncrease}>Increase</button>
      {/* <footer><div>copyright</div></footer> */}
    </div>
  );
}
