import counter_reducers from "./reducer/reducers";
import { combineReducers, createStore, applyMiddleware } from "redux";
import { configureStore } from "@reduxjs/toolkit";
import createSagaMiddleware from "redux-saga";
import { counter_saga } from "./action/actions_saga";
import reducer_saga from "./reducer/reducer_saga";

const red = combineReducers({ counter_reducers, reducer_saga });
// console.log(red);
const sagaMiddleware = createSagaMiddleware();

export const store = createStore(red, applyMiddleware(sagaMiddleware));
// console.log(store);

// export const store = configureStore({counter_reducers});

sagaMiddleware.run(counter_saga);
