import { takeEvery, put } from "redux-saga/effects";

function* increase_counter() {
  const data = yield fetch("https://jsonplaceholder.typicode.com/todos");
  const res = yield data.json();
  console.log("SAGA + COUNTER:", res);
  yield put({ type: "SETDATA", payload: res });
}
function* decrease_counter() {
  console.log("SAGA - COUNTER");
  // yield put("INCREASE");
}

export function* counter_saga() {
  console.log("SAGA");
  yield takeEvery("INCREASE", increase_counter);
  yield takeEvery("DECREASE", decrease_counter);
}
